.container {
    display: flex;
    flex-direction: column;
    width: 100vw;

    background-color: #000000;
    color: #FFFFFF;

    user-select: none;
}

.nav {
    position: absolute;
    top: 0px;
    right: 0px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;

    flex-wrap: wrap;
    
    /* background-color: #FFFFFF;
    color: #000000; */

    width: 100vw;

    padding: 10px 20px;
}

.navItem {
    font-size: 1.5rem;
    font-weight: 300;
    cursor: pointer;

    text-decoration: none;
    color: #FFFFFF;

    margin: 0px 10px;
}
.navItem:hover {
    text-decoration: none;
}

.socials {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: fit-content;
    margin: 20px 0px;
}

.socialItem {
    cursor: pointer;
    margin: 0px 5px;
}


.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;

    background-color: #000000;
    color: #FFFFFF;
}

.welcome {
    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 50px 0px;
}

.name {
    font-size: 4rem;
    font-weight: 500;
}

.subName {
    font-size: 2rem;
    font-weight: 300;
}

.stacks {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    
    flex-wrap: wrap;

    margin: 20px 0px;
}

.stackItem {
    font-size: 1.5rem;
    font-weight: 300;
    cursor: pointer;

    width: 30px;
    height: 30px;
    /* background-color: #FFFFFF; */

    margin: 10px 10px;
}

.stackWrapper {
    display: flex;
    flex-direction: column;

    align-items: center;
    justify-content: flex-start;

    width: 100vw;
}

.showStacks {
    margin: 0px 20px;
    font-size: 1.5rem;
    font-weight: 300;
    user-select: none;
}


.about {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    width: 100vw;
    height: 100vh;

    border-top: 1px solid #FFFFFF;
    border-bottom: 1px solid #FFFFFF;
}

.projects {
    display: flex;
    flex-direction: column;

    width: 100vw;

    border-bottom: 1px solid #FFFFFF;
}

.body {
    display: flex;
    flex-direction: column;

    width: 100vw;

}

.footer {
    position: relative;
    display: flex;
    flex-direction: column;

    width: 100vw;

}



@media screen and (max-width: 600px) {
    .navItem {
        font-size: 1rem;
        margin: 0px 5px;
    }

    .name {
        font-size: 2rem;
    }

    .subName {
        font-size: 1rem;
    }
}