.container {
    display: flex;
    flex-direction: row;
    height: 200px;
    width: 100%;

    /* background-color: transparent; */
    background-color: #FFF;
    color: #000;
    cursor: pointer;
    user-select: none;

    margin: 20px 0px;
}

.imageContainer {
    width: 100%;
    height: 100%;

    filter: blur(5px);
    -webkit-filter: blur(8px);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.image {
    position: absolute;
    /* top: -25%;
    left: -25%; */
    width: 100%;
}

.content {
    display: flex;
    height: 100%;
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0px 20px;
}

.title {
    font-size: 2.5rem;
    font-weight: 500;
    margin: 10px 0px;
}

.description {
    font-size: 1rem;
    font-weight: 300;
    margin: 10px 0px;
}

.tags {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    width: 20%;

    /* border: 1px solid #0000FF; */
}

.tag {
    font-size: 1rem;
    font-weight: 300;
    margin: 10px 0px;
}